export default {  
        name: 'compInput',  
        behaviors: ['uni://form-field'],
		data(){
			return{
				modalName:null
			}
		},
		
        methods: {  
            onInput(e) {  
                this.$emit('input', e.detail.value)  
            }  ,
			showModal(e) {
				console.log(123);
				this.modalName = "DialogModal"
			},
			hideModal(e) {
				this.modalName = null
			},
			to(name){
				this.modalName = null
				console.log('关闭')
				console.log('跳转');
				uni.navigateTo({
					
					url:name
				});
			},
        }  
    }  
